.container {
    border: 1px solid;
    width: 80%;
    height: 100%;  
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 5px;
}

@media (max-width: 840px) {
    .container {
        justify-content: center;
    }
}